<template>
    <div  @mouseenter="active1" >

        <Button class="back1"
                type="primary"
                @click="isactive"
                icon="md-arrow-dropright-circle"
                style="border-radius: 20%;"></Button>
        <div class="player" v-show="seen" >

            <Button class="back" type="primary" icon="ios-skip-backward" @click="before"  ></Button>

            <Button class="back2" type="primary" icon="md-arrow-dropright-circle" :icon="icon" @click="rotate"  ></Button>

            <Button class="back" type="primary" icon="ios-skip-forward"  @click="next" ></Button>
          <div class="bg" :class="{rotatey:active}" ><img :src="$store.state.song_image_url" alt=""></div>
            <Button class="back3"  icon="md-heart-outline"></Button>
          <audio id="ad" ref="ad"  :src="$store.state.musicurl"  ></audio>

          <h5>{{$store.state.songs}}</h5>
          <h6 id="h6singer" >{{$store.state.singer}}</h6>
          <h6 id="h6sd" >{{$store.state.currentmin+':'+$store.state.currentsec}}/{{$store.state.minutes+':'+$store.state.seconds}}</h6>

            <Slider class="slider" v-model="$store.state.value4"
                    @on-change="nextTick"
                    :step="1"></Slider>

        </div>


    </div>
</template>

<script>
  import Action from './action/musicIndex'
  const dataFactory = (params) => {
    return params ? params : {
      MusicCode: '',
      songs_name:'',
      singer:'',
      songs_id:'',
      song_file_url:'',
      song_image_url:'',
      songs_file_id:'',
      uploadStatus: null,
      uploadList: [],
      ShowOnIndex: false,
      edit: false
    }
  }
    export default {
        name: "player",
      data(){
          return{
            tableData:[{}],
            seen:false,
            value4:0,
            value:"",
            musicurl: '',
            createModal:dataFactory(),
            active:false,
            de:'',
            index:0,
            myVid: null,
            icon: 'ios-play-outline',
            flag:false,
          }
      },
      mounted() {
        this.myVid = this.$refs.ad;
        this.showtime();
        this.paramsShowHandler();
      },

      methods:{

        nextTick(e){

          this.$nextTick(()=>{
            let currenttime = (e / 100) * this.myVid.duration;
            this.myVid.currentTime = currenttime;
          })
        },
        //上一首歌曲
        before(){
          this.active=true
          this.icon="ios-pause"
          this.index--;
          if (this.index == -1) this.index = this.tableData.length - 1;
          this.$store.state.musicurl = this.tableData[this.index].song_file_url;
          this.$store.state.songs = this.tableData[this.index].songs_name;
          this.$store.state.singer = this.tableData[this.index].singer;
          this.$store.state.song_image_url = this.tableData[this.index].song_image_url;
          this.myVid.load()
          setTimeout(() => {
            this.myVid.play();
          }, 100)
        },

        //下一首歌曲
        next(){
          this.active=true
          this.icon="ios-pause"
          this.index++;
          if (this.index == this.tableData.length) this.index = 0;
          this.$store.state.musicurl = this.tableData[this.index].song_file_url;
          this.$store.state.songs = this.tableData[this.index].songs_name;
          this.$store.state.singer = this.tableData[this.index].singer;
          this.$store.state.song_image_url = this.tableData[this.index].song_image_url;
          this.myVid.load()
          setTimeout(() => {
            this.myVid.play();
          }, 100)
        },
        //显示当前时间
        showtime(){
          return this.myVid.ontimeupdate = () => {
            //当前时长
            let currenttime = this.myVid.currentTime;
            this.$store.state.currentmin = parseInt(currenttime / 60);
            this.$store.state.currentsec = parseInt(currenttime % 60);
            this.$store.state.currentmin=(this.$store.state.currentmin <10?("0"+this.$store.state.currentmin):this.$store.state.currentmin);
            this.$store.state.currentsec=(this.$store.state.currentsec <10?("0"+this.$store.state.currentsec):this.$store.state.currentsec);
            //总时长
            let time = this.myVid.duration;
            this.$store.state.minutes = parseInt(time / 60, 10);
            this.$store.state.seconds = parseInt(time % 60);
            this.$store.state.minutes=(this.$store.state.minutes <10?("0"+this.$store.state.minutes):this.$store.state.minutes);
            this.$store.state.seconds=(this.$store.state.seconds <10?("0"+this.$store.state.seconds):this.$store.state.seconds);
            this.$store.state.value4 = Math.floor((currenttime / (time / 100)))
            if (currenttime === time) {
              this.icon="ios-pause"
              this.active=true
              this.index++;
              if (this.index == this.tableData.length) this.index = 0;
              this.$store.state.musicurl = this.tableData[this.index].song_file_url;
              this.$store.state.songs = this.tableData[this.index].songs_name;
              this.$store.state.singer = this.tableData[this.index].singer;
              this.$store.state.song_image_url = this.tableData[this.index].song_image_url;
              this.myVid.load()
              setTimeout(() => {
                this.myVid.play();
              }, 200)
            }
          }
        },

        rotate(){
          this.$refs.ad.play()
          if(this.active==false)
          {
            this.active=true
            this.icon="ios-pause";
            this.myVid.play();

          }
          else{
            this.active=false
            this.icon="ios-play-outline";
            this.myVid.pause();
          }
        },  //根据参数权限码显示页面
        paramsShowHandler(){
          // let _filter = objectCopy(this.filter);
          // for (let i in _filter) {
          //   if (!_filter[i]) delete _filter[i]
          // }
          Action.musicGetList({
            // PostContent: {
            //   filter: _filter
            // }
          })
            .then(res => {
              this.tableData = res.map(item=>{
                item.song_image_url = `${REQUEST_URL.IndexImageDownload}${item.song_image_url}`;
                item.song_file_url = `${REQUEST_URL.IndexImageDownload}${item.song_file_url}`;
                return item;
              });
              this.$store.state.singer = this.tableData[this.index].singer;
              this.$store.state.songs = this.tableData[this.index].songs_name;
              this.$store.state.musicurl = this.tableData[this.index].song_file_url;
              this.$store.state.song_image_url = this.tableData[this.index].song_image_url;
            })
            .catch(err => {
              console.log(err)
              this.$Message.error(err);
            })
        },
        active1(){
            this.seen = true;
          },
        isactive(){
          this.seen=!this.seen;
          console.log('sd');
        },

      }
    }
</script>

<style scoped>
  .player{
    position: fixed;
    bottom: .3em;
    right: 0;
    width: 80%;
    height: 4em;
    background-color: rgb(49,48,48);
    border-radius: 15%;
    /*background-image: url("../../assets/bg.jpg");*/
    /*background-size: contain;*/
    /*background-repeat: repeat-x;*/

  }
  .back1{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 20%;
    height: 4em;

  }
  .back2{
    margin-left: 2em;
    margin-top: -2.2em;
    border-radius: 10%;
  }
  .back{
    border-radius: 50%;
    margin-top: -2.2em;
    margin-left: 2em;
  }
  .slider{
    position: relative;
    top: -6em;
    left: 25em;
    width: 30%;
  }
  #h6sd{
    position: relative;
    top: -6em;
    left: 76em;
    width: 30%;
    color: white;
  }
  #h6singer{
    position: relative;
    top: -6.2em;
    left: 50em;
    width: 30%;
    color: white;
  }
  h5{
    position: relative;
    top: -3.8em;
    left: 30.2em;
    width: 30%;
    color: white;
  }
  .back3{
    margin-top: -2.2em;
    margin-left: 30em;
    border-radius: 50%;
  }
  .bg{
    height: 50px;
    width: 50px;
    display: inline-block;
    overflow: hidden;
    position: relative;
    margin-top: .2em;
    margin-left: 5em;

  }
  .bg img{
    height: 100%;
    width: 100%;
    border-radius:50%;

  }
  .rotatey{
    -webkit-transform: rotate(360deg);
    animation: rotation 20s linear infinite;
    -moz-animation: rotation 20s linear infinite;
    -webkit-animation: rotation 20s linear infinite;
    -o-animation: rotation 20s linear infinite;
  }
  @-webkit-keyframes rotation{
    from {-webkit-transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);}
  }
</style>



