const Action={
  //获取参数列表
  musicGetList (params) {
    return axios.post(REQUEST_URL.musicGetList, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //删除参数
  MusicDeleteFiles (params) {
    return axios.post(REQUEST_URL.MusicDeleteFiles, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //获取音乐
  MusicgetFiles(params){
    return axios.post(REQUEST_URL.MusicgetFiles, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  },
  //获取图片
  MusicgetImage(params){
    return axios.post(REQUEST_URL.MusicgetImage, REQUEST_URL.handleParams(params)).then(result => Promise.resolve(result.data.PostContent));
  }

}

export default Action;



