import Vuex from 'vuex';
import Vue from 'vue';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        //是否登陆
        IsLogin: false,
        //用户信息
	    UserInfo: null,
        //用户Token
        UserToken: null,
	   //控制左侧菜单是否收缩
        pageNavShrink: false,
        //是否显示左侧菜单
        showMenu: true,
        //全局缓存视图
        keepAliveViews: [],
        //是否显示后台管理菜单
        showAdminMenu: false,
        //480px屏幕菜单
        navshow_480px: false,
      //音乐文件
        Musicurl:null,
      //总分钟
        minutes:"00",
      //总秒
      seconds:"00",
      //当前分钟
      currentmin:"00",
      //当前秒
      currentsec:"00",
      //当前时间
      currentTIme: '',
      //播放音乐
      play:false,
      //滑块值
      value4:0,
      //歌手
      singer:"",
      //歌曲
      songs:"",
      song_image_url:"",
    },
    mutations: {
        pageNavShrink(state, value){state.pageNavShrink = value},
        navshow_480px(state, value){state.navshow_480px = value},
        showMenu(state, value){state.showMenu = value},
        keepAliveViews(state, value){state.keepAliveViews = value},
        showAdminMenu(state, value){state.showAdminMenu = value},
        UserInfo(state, value){state.UserInfo = value},
        Musicurl(state, value){state.Musicurl = value},
      minutes(state, value){state.minutes = value},
      seconds(state, value){state.seconds = value},
      currentmin(state, value){state.currentmin = value},
      currentsec(state, value){state.currentsec = value},
      play(state, value){state.play = value},
      value4(state, value){state.value4 = value},
      singer(state, value){state.singer = value},
      songs(state, value){state.songs = value},
      song_image_url(state, value){state.song_image_url = value},
    }
})



